	module.exports = {"data":{"markdownRemark":{"html":"<p>私は今日、大統領を見ました。彼は日本から帰国し、日本の \"情報ハイウェイ\"の研究室を訪問しました。</p>\n<p>かなりショックを受けた。日本はすでに300人の実験計画を開始している。デザインによると、Toflerは「電子キャビン」と呼んでいます。その後、人々は家庭で働くことになり、すべての文書はコンピュータシステムを通じて処理され、統一されたオフィスで働く必要はなく、人々は家庭で勉強し、コンピュータシステム内の世界最高の教授コースでは、両方向で話すことができます質問に答えて、人々は家で買うことができる、様々な製品は、選択後に店舗によって選択され、家で見ることができる、薬の様々な直接コンピュータから選択することができますX線そのような治療は特別な車両によって送信することができ、人々は自宅で何百もの映画を選択して統一された処理システムによって家庭に放送することができ、世界中のTV番組を選ぶことができます。各国語のコストの自動翻訳など。これらはあまり遠いものではなく、既に見られたものがすでに出てきており、すでに出てきたものもあります。もともとサイエンスフィクションで説明されていたことが現実になりつつあります。</p>\n<p>より重要な課題は、人々のライフスタイルを変え、社会の管理方法を変え、社会が組織される方法を変えることです。オフィスビルのほとんどが必要なくなった場合、ほとんどの新聞は必要ありませんが、ほとんどの大型ショッピングモールには必要ありません。構造的な問題。社会はそれをどのように管理するのか、イデオロギーの役割はどのようになるのか、政府の経営スタイルはどう変わるのだろうか。</p>\n<p>このあまり遠い将来の見通しについては遠くのビジョンがあるはずです。</p>\n<p>今日、上海テレビ祭が開かれ、新設のオリエンタルテレビ塔の下で開会式が行われました。新たに建設されたオリエンタルテレビ塔は、夜空に立っており、灯篭の照明の下で壮観です。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0183.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0183.md","frontmatter":{"title":"11月12日 土曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0183.md absPath of file >>> MarkdownRemark"}}

